import React, { useEffect, useState } from "react";
import {
  Box,
  Stack,
  Typography,
  Modal,
  RadioGroup,
  FormControlLabel,
  Radio,
  TextField,
  Button,
  IconButton,
  List,
  ListItem,
  ListItemAvatar,
  Avatar,
  ListItemText,
  styled,
} from "@mui/material";

// axios import removed as it was unused
import { api } from "api/API";
import HeaderTypography from "app/formComponents/HeaderTypography";
import { ArrowForward, Clear } from "@mui/icons-material";
import appStore from "app/mobxStore/AppStore";
import ChecklistRtlOutlinedIcon from "@mui/icons-material/ChecklistRtlOutlined";
import { Themecolors, fonts } from "api/Colors";
import { observer } from "mobx-react";
import DocStore from "app/mobxStore/DocumentStore";
import { blue } from "@mui/material/colors";
import { User } from "@auth0/auth0-spa-js";
import SupervisorAccountIcon from "@mui/icons-material/SupervisorAccount";

const StyledActionButton = styled(Button)(({ theme }) => ({
  margin: theme.spacing(0, 1),
  backgroundColor: "#ffffff",
  justifyContent: "space-between",
  color: "#000",
  fontWeight: 600,
  fontFamily: fonts.poppins,
  marginRight: 50,
  height: "55px",
  width: "95%",
  "&:hover": {
    backgroundImage: Themecolors.B_hv3,
    backgroundColor: "#ffffff",
  },
}));

const OptionContainer = styled(Box)(({ theme }) => ({
  backgroundColor: "#efefef",
  borderRadius: "5px",
  padding: "15px",
  transition: "all 250ms ease-in-out",
}));

const StyledNoteField = styled(TextField)(({ theme }) => ({
  margin: "5px",
  width: "100%",
  fontFamily: fonts.inter,
}));

const SubmitButton = styled(Button)(({ theme }) => ({
  width: "100%",
  margin: "5px",
  borderColor: Themecolors.Button1,
  backgroundColor: Themecolors.Button1,
  color: Themecolors.Button2,
  fontFamily: fonts.inter,
  "&:hover": {
    backgroundImage: Themecolors.B_hv1,
    borderColor: Themecolors.Button1,
    backgroundColor: Themecolors.Button1,
  },
}));

interface Props {
  documentStateId: number;
  taskId: number;
  documentTypeId?: number;
  currentUser: number;
  userType: number;
  onActionComplete: (
    message: string,
    severity: "error" | "success" | "info" | "warning"
  ) => void;

  assignedApprovers: User[];
}

interface Action {
  actionId: number;
  actionName: string;
  actionStateId: number;
  options: Option[];
}

interface Option {
  id: number;
  name: string;
  description: string;
}

const ActionButtons: React.FC<Props> = observer(
  ({
    documentStateId,
    taskId,
    documentTypeId,
    currentUser,
    userType,
    onActionComplete,
    assignedApprovers,
  }) => {
    const [actions, setActions] = useState<Action[]>([]);
    const [currentStateName, setCurrentStateName] =
      useState<string>("Actions State");
    const [modalOpen, setModalOpen] = useState<boolean>(false);
    const [selectedActionOptions, setSelectedActionOptions] = useState<
      Option[]
    >([]);
    const [currentAction, setCurrentAction] = useState<Action>();
    const [selectedOption, setSelectedOption] = useState<number | null>(null);
    const [note, setNote] = useState<string>("");
    const [actionCompleteTrigger, setActionCompleteTrigger] = useState(false);
    const userId = appStore.loginResponse.user[0].userId;
    const canAction = assignedApprovers.some((user) => user.userId === userId);
    useEffect(() => {
      if (canAction) loadTaskActions();
    }, [documentStateId, taskId, documentTypeId]);

    const isButtonDisabled =
      (selectedActionOptions?.length || 0) === 0
        ? note.trim() === ""
        : selectedOption === null;

    const loadTaskActions = () => {
      api
        .post(`task-actions`, {
          body: {
            taskId: taskId,
            userId: currentUser,
            userRoles: appStore.loginResponse.user[0].roles,
          },
        })
        .then((response) => {
          setActions(response.data);
          DocStore.setActions(response.data);
        })
        .catch((error) => {
          console.error("Error fetching action data:", error);
        });
      api
        .post(`get-document-state`, {
          body: {
            documentStateId: documentStateId,
          },
        })
        .then((response) => {
          if (response.status) setCurrentStateName(response.data);
        });
    };

    const handleAction = async (action: Action) => {
      setSelectedActionOptions(action.options);
      setModalOpen(true);
      setCurrentAction(action);
      setSelectedOption(null);
      setNote("");
    };

    const actionSubmit = async (actionId: number) => {
      const selected = selectedActionOptions?.find(
        (o) => o.id === selectedOption
      ).name;
      api
        .post(`add-task-workflow-action`, {
          body: {
            taskId: taskId,
            taskSelectedOption: selected ?? "",
            taskNote: note,
            taskUserId: currentUser,
            taskActionId: actionId,
          },
        })
        .then((res) => {
          if (res.status) {
            api
              .post(`update-action`, {
                body: {
                  taskId: taskId,
                  actionId: actionId,
                  userId: currentUser,
                },
              })
              .then((res) => {
                if (res.status) {
                  // onActionComplete(res.message, "success");
                  // setActionCompleteTrigger((state) => !state);
                  window.location.reload();
                } else {
                  onActionComplete(res.message, "error");
                }
                setCurrentAction(null);
                setNote("");
                setSelectedOption(null);
              });
          }
        });
    };
    const handleSubmit = () => {
      // Implement the API call or logic to submit the selected option and note
      setModalOpen(false);
      actionSubmit(currentAction.actionId);
      setNote("");
    };

    const handleOptionChange = (event: React.ChangeEvent<HTMLInputElement>) => {
      setSelectedOption(parseInt(event.target.value, 10));
    };

    return (
      <Box
        sx={{
          height: "100%",
          maxWidth: "84%",
          backgroundColor: "#efefef",
        }}
      >
        <HeaderTypography title={currentStateName} />
        <div
          style={{
            maxHeight: "calc(102vh - 150px)",
            overflowY: "auto",
            overflowX: "hidden",
          }}
        >
          {actions.length == 0 && (
            <>
              <Box sx={{ display: "flex", alignItems: "center", mt: 5, ml: 3 }}>
                <ChecklistRtlOutlinedIcon
                  sx={{ color: "#cfcfcf", fontSize: 35, mr: "5px", mt: "3px" }}
                />
                <Typography
                  sx={{
                    color: "#cfcfcf",
                    mt: 0.5,
                    mb: 0,
                    fontSize: 21,
                    fontWeight: 450,
                    fontFamily: fonts.poppins,
                  }}
                >
                  No actions available
                </Typography>
              </Box>
            </>
          )}
          <Stack
            spacing={2}
            direction="column"
            mt={2}
            sx={{
              paddingBottom: "10px",
            }}
          >
            {actions.map((action) => (
              <div key={action.actionId}>
                <StyledActionButton
                  variant="contained"
                  endIcon={<ArrowForward />}
                  onClick={() => handleAction(action)}
                >
                  {action.actionName}
                </StyledActionButton>
                {currentAction?.actionId == action.actionId && (
                  <OptionContainer key={"option-container"}>
                    <Box
                      display={"flex"}
                      justifyContent={"right"}
                      alignItems={"center"}
                    >
                      <IconButton
                        size="small"
                        sx={{ textAlign: "right" }}
                        onClick={() => {
                          setCurrentAction(null);
                          setNote("");
                          setSelectedOption(null);
                        }}
                      >
                        <Clear fontSize="small" />
                      </IconButton>
                    </Box>

                    <RadioGroup
                      value={selectedOption}
                      onChange={handleOptionChange}
                    >
                      {selectedActionOptions?.map((option) => (
                        <FormControlLabel
                          value={option.id}
                          control={<Radio />}
                          label={option.name}
                          key={option.id}
                        />
                      ))}
                    </RadioGroup>
                    <Box flexDirection={"column"} width={"90%"}>
                      <StyledNoteField
                        label="Note"
                        multiline
                        rows={4}
                        variant="outlined"
                        value={note}
                        onChange={(e) => setNote(e.target.value)}
                        onKeyDown={(e) => {
                          if (e.key === "Enter") {
                            e.preventDefault();
                            setNote((prev) => prev + "\n");
                          }
                        }}
                      />
                      <SubmitButton
                        onClick={handleSubmit}
                        variant="contained"
                        color="success"
                        disabled={isButtonDisabled}
                      >
                        Submit
                      </SubmitButton>
                    </Box>
                  </OptionContainer>
                )}
              </div>
            ))}
            <HeaderTypography>Task Approvers</HeaderTypography>
            {assignedApprovers.length === 0 ? (
              <>
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    mt: 8,
                  }}
                >
                  <Box
                    sx={{
                      display: "flex",
                      justifyContent: "center",
                      ml: "22px",
                      mt: 1,
                    }}
                  >
                    <SupervisorAccountIcon
                      sx={{
                        color: "#cfcfcf",
                        fontSize: 27,
                        mr: "5px",
                        mt: "2px",
                      }}
                    />
                    <Typography
                      sx={{
                        color: "#cfcfcf",
                        mt: 0.5,
                        mb: 0,
                        fontSize: 16,
                        fontWeight: 450,
                        fontFamily: fonts.poppins,
                      }}
                    >
                      No Task Approvers available{" "}
                    </Typography>
                  </Box>
                </Box>
              </>
            ) : (
              <List
                style={{
                  overflowY: "auto",
                  marginBottom: "25px",
                }}
              >
                {assignedApprovers.map((user) => (
                  <React.Fragment>
                    <ListItem
                      key={user.userId}
                      style={{ alignItems: "flex-start" }}
                    >
                      <ListItemAvatar>
                        <Avatar
                          sx={{ backgroundColor: blue[100], color: blue[600] }}
                        >
                          {user.userFirstName[0] + user.userLastName[0]}
                        </Avatar>
                      </ListItemAvatar>
                      <ListItemText
                        primary={`  ${user.userName}`}
                        secondary={`${user.userFirstName} ${user.userLastName} `}
                      />
                    </ListItem>
                  </React.Fragment>
                ))}
              </List>
            )}
          </Stack>
        </div>
      </Box>
    );
  }
);

export default ActionButtons;
